<template>
  <div class="wrapper">
    <main class="c_main">
      <div class="pf_login">
        <p class="pf_login_title">pass</p>
        <p
          class="pf_login_miss"
          v-if="loginMiss"
        >
        パスワードが違います。
        </p>
        <input
          type="password"
          class="pf_login_pass"
          placeholder="パスワードを入力してください"
          v-model="loginPass"
        />
        <button
          class="pf_login_btn"
          @click="goLogin"
        >
        login
        </button>
      </div>
    </main>
  </div>
</template>

<script>

export default {
  name: 'login',
  data() {
    return {
      loginPass: "",
      loginMiss: false
    }
  },
  computed: {
  },
  mounted() {
    let lState = this.$store.getters.logined
    if(lState){
      this.$store.commit('setLogined', false)
    }
  },
  methods: {
    goLogin () {
      let setPass = "c201901h"
      if(this.loginPass === setPass){
        this.loginMiss = false
        this.$router.push(`/item/`)
        this.$store.dispatch('checkLogin')
      }else{
        this.loginMiss = true
      }
    }
  }
}
</script>

